.root {
  width: 32px;
  height: 32px;
  background-color: white;
  border-radius: 4px;
  border: 1px solid #cccccc;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 6px;
  cursor: pointer;
}

.root:hover {
  compose: root;
  background-color: #e1e1e1;
}
